  /*=== Methods to collect data for intercom ===*/
  getAllLeadsCount(user_status: string = '') {
    const self = this;
    const getLeadsCount = self._calculatorAnalytics.getLeadsCount(this.company_id)
      .subscribe(
        (success: any) => {
          let cp = 0;
          this.apps.forEach(function (app) {
            if (app.liveApp) { cp++; }
          });
          let icd = this._cookieService.readCookie('icd');
          icd = icd ? JSON.parse(icd) : {};
          const interComData = Object.assign({
            'leads': success.leads_count,
            'subscription_status': user_status,
            'calculators_created': this.apps.length,
            'calculators_published': cp
          }, icd);
          localStorage.setItem('icd', JSON.stringify(interComData));
          window.Intercom('update', interComData);
        },
        (error: any) => {
          console.log('Error');
        }
      );
    this.subs.push(getLeadsCount);
  }

  getPlanSubscription() {
    const self = this;
    let user_status = '';
    const getPlanSubscription = this._membershipService.getPlanSubscription()
      .subscribe(
        (success: any) => {
          this.subscription = new Subscriptions(success.currentplan.subscription);
          switch (this.subscription.status) {
            case 'in_trial':
              user_status = 'Trial';
              let difference;
              this.countdownPromoService.subscription = this.subscription;
              const trialEnd = moment.unix(success.currentplan.subscription.trial_end);
              this.countdownPromoService.setCountdownTimer(trialEnd);
              if (this.countdownPromoService.trialEnd) {
                difference = moment.duration(this.countdownPromoService.trialEnd.diff(moment(new Date())));
                if (difference.asSeconds() < 86400 && difference.asSeconds() >= 0) { // show promo before 24 hours
                  this.showCountdownPromo = true; // when trial is about to expire
                }
              }
              const today = moment(new Date());
              const trialStartedAt = moment.unix(success.currentplan.subscription.trial_start);
              this.dayOfTrial = today.diff(trialStartedAt, 'days');
              const trialLeftDays = trialEnd.diff(today, 'days');
              //  this.dayOfTrial = 2;
              if (this.dayOfTrial >= 1) {
                this.showUpgradeOffer = true;
              }
              if (this.dayOfTrial >= 5) {
                this.showAffiliate = true;
              }
              if ([2].includes(this.dayOfTrial) && !this.isMobileDevice) {
                setTimeout(() => {
                  if (!this.userPhoneEntered) {
                    jQuery('#phone-popup').modal('show');
                  }
                }, 5 * 60 * 1000);
              }
              if (trialLeftDays < 3 && !self._cookieService.readCookie('viewed')) {
                setTimeout(() => {
                  jQuery('#modal-bonus-offer1').modal('show');
                  self._cookieService.createCookie('viewed', 'offer', 1);
                }, 1000);
              }
              break;
            case 'active':
              user_status = 'Paid';
              if (this.subscription.plan_id === 'starter') {
                user_status = 'Free';
              }
              this.showAffiliate = true;
              break;
          }
          this.getAllLeadsCount(user_status);
          // this.loader = 1;
          // setTimeout(() => this.removeOverlay(), 11000);
        },
        (error: any) => {
          /*getPlanSubscription.unsubscribe();*/
        }
      );
    this.subs.push(getPlanSubscription);
  }

