  getCompanyProjects() {
    this.calcIds = [];
    return this.companyService.getCompanyProjects(this.sub_domain)
      .subscribe(
        (response: any[]) => {
          this.totalUniqueVisitors = 0;
          this.totalLeads = 0;
          this.totalCta_Engagement = 0;
          this.overallConversionRate = 0;
          let i = 0;
          this.foldersContent = response;
          const parsedCookie = JSON.parse(this.cookie);

          if (!this.foldersContent['premadeFolder'] && !parsedCookie['postSignup']) {
            this.createPremadeFolder({
              name: 'premade content', industries: ['Trending'],
              company_id: this.subDomainService.currentCompany.id
            });
          }
          const pinnedApps = [];
          if (this.foldersContent['folders'] && this.foldersContent['folders'].length) {
            this.foldersRef = this.foldersContent['folders'].map(f => {
              if (f['pinned_apps']) {
                f['pinned_apps'] = f['apps'].filter((app) => {
                  if (f['pinned_apps'].includes(app._id)) {
                    app['pinned'] = true;
                    pinnedApps.push({
                      link: ((app.pages[0].visible && app.pages[0].bgImageVisible) || (app.pages[0].bgImageVisible)) ? app.pages[0].bgImage : this.templatesImages[app.template],
                      id: `${app._id}_image_pin`, loaderId: `${app._id}_loader_pin`
                    });
                    return true;
                  }
                  app['pinned'] = false;
                  return false;
                });
              } else {
                f['pinned_apps'] = [];
              }
              return f;
            });
            this.initalizeFolderSettings();
          }
          const totalApps = this.getAppsFromFolders(response['folders'], response['leaves']);
          this.isAnyLiveCalc = totalApps.some((app) => app['liveApp']);
          const array: any = [];
          setTimeout(this.initializeSwiper.bind(this), 100);
          this.apps = totalApps.map((app: any) => {
            i++;
            app.created_at = app.createdAt;
            app.updated_at = app.updatedAt;
            app.createdAt = moment(app.liveApp ? app.liveApp.createdAt : app.createdAt).format("MMM Do 'YY");
            app.updatedAt = moment(app.updatedAt).format("MMM Do 'YY");
            if (app.liveApp) {
              this.calcIds.push(app._id);
              // this.subs.push(this.getStats(app));
            } else {
              if (!app.name.length) {
                app.name = this.subDomainService.subDomain.name + "'s calculator #" + i;
              }
            }
            array.push({
              link: ((app.pages[0].visible && app.pages[0].bgImageVisible) || (app.pages[0].bgImageVisible)) ? app.pages[0].bgImage : this.templatesImages[app.template],
              id: `${app._id}_image`, loaderId: `${app._id}_loader`
            });
            return app;
          });

          this.getStatsV2(this.sub_domain);
          this.getPremadeCalcDetails();
          // this.apps.push(this.getDemoAppData());
          this.maxPageCount = Math.ceil(this.apps.length / this.pageSize);
          this.getPlanSubscription();
          // Temporary stuff
          this.tempStuff();
          this.updateIntercomforPage();
          this.getCurrentCompanyDetails();
          // this.lazyLoadImages([...array, ...pinnedApps]);
          this.loader = 1;

        },
        (error: any) => {
        });
  }

