  imgLoader(link, id, loaderId) {
    return new Promise((resolve, reject) => {
      const img = new Image();
      img.src = link;
      img.onload = function () {
        let ele = document.getElementById(`${id}`);
        let loaderDiv = document.getElementById(`${loaderId}`);
        if (ele) {
          ele.classList.remove('hide');
          ele.style.backgroundImage = `url('${img.src}')`;
          loaderDiv && (loaderDiv.classList.add('hide'));
        }

        resolve(true);
      };
    });

  }
