  checkSubDomain(url: String) {
    // trim spaces
    url = url.replace(/^\s+/, '');
    url = url.replace(/\s+$/, '');

    // convert back slash to forward slash
    url = url.replace(/\\/g, '/');

    // remove 'http://', 'https://' or 'ftp://'
    url = url.replace(/^http\:\/\/|^https\:\/\/|^ftp\:\/\//i, '');

    // remove 'www.' if exist
    url = url.replace(/^www\./i, '');
    if (url.split('.').length === 3 && url.split('.')[0] === 'app') {
      return false;
    }

    // remove path after domain
    url = url.replace(/\/(.*)/, '');

    // remove tld's
    if (url.match(/\.[a-z]{2,3}\.[a-z]{2}$/i)) {
      url = url.replace(/\.[a-z]{2,3}\.[a-z]{2}$/i, '');
    } else if (url.match(/\.[a-z]{2,5}$/i)) {
      url = url.replace(/\.[a-z]{2,5}$/i, '');
    }

    return (url.match(/\./g)) ? true : false;
  }
  getStatsV2(subDomain: string) {
    return this.companyService.getCalcSDashboardStats(this.calcIds, subDomain)
      .subscribe(
        (response) => {
          let statsObject = {};
          if (response.type === 'REAL_TIME') {
            response.stats[0].map((res) => {
              statsObject[res._id] = { leads: res.totalLeads };
            });
            response.stats[1].map((res) => {
              statsObject[res._id] ? (statsObject[res._id]['uniqueViews'] = res.totalVisits) : (statsObject[res._id] = { uniqueViews: res.totalVisits });
            });
            this.apps.map((app) => {
              app.leads_in_num = (statsObject[app._id] && statsObject[app._id]['leads']) ? statsObject[app._id]['leads'] : 0;
              app.leads = (statsObject[app._id] && statsObject[app._id]['leads']) ? this.prettify(statsObject[app._id]['leads']) : 0;
              app.uniqueViews_in_num = (statsObject[app._id] && statsObject[app._id]['uniqueViews']) ? statsObject[app._id]['uniqueViews'] : 0;
              app.uniqueViews = (statsObject[app._id] && statsObject[app._id]['uniqueViews']) ? this.prettify(statsObject[app._id]['uniqueViews']) : 0;
            });
          }
          if (response.type === 'PRE_CALCULATED') {
            response.stats.map((res) => {
              statsObject[res._id] = { leads: res.leadsCount, uniqueViews: res.visitorsCount };
            });
            this.apps.map((app) => {
              app.leads_in_num = (statsObject[app._id] && statsObject[app._id]['leads']) ? statsObject[app._id]['leads'] : 0;
              app.leads = (statsObject[app._id] && statsObject[app._id]['leads']) ? this.prettify(statsObject[app._id]['leads']) : 0;
              app.uniqueViews_in_num = (statsObject[app._id] && statsObject[app._id]['uniqueViews']) ? statsObject[app._id]['uniqueViews'] : 0;
              app.uniqueViews = (statsObject[app._id] && statsObject[app._id]['uniqueViews']) ? this.prettify(statsObject[app._id]['uniqueViews']) : 0;
            });
          }
          if (this.filters && this.filters.length) {
            this.filters.forEach(f => {
              this.filterContents(f.type, f.order);
            });
          }
          this.loadAllImages();
        },
        (error: any) => {
        });
  }
  getStats(app: any) {
    return this.companyService.getProjectsStats(app._id)
      .subscribe(
        (response: any[]) => {
          if (response.length) {
            app.uniqueViews = Number(response[0][1]);
            app.leads = Number(response[0][3]);
            app.Cta_Engagement_Count = Number(response[0][4]);
            app.conversionRate = (app.uniqueViews && app.leads) ? ((app.leads / app.uniqueViews) * 100).toFixed(2) : 0;

            this.totalUniqueVisitors += app.uniqueViews;
            this.totalCta_Engagement += app.Cta_Engagement_Count;
            this.totalLeads += app.leads;
            this.overallConversionRate = (this.totalUniqueVisitors) ? ((this.totalLeads / this.totalUniqueVisitors) * 100).toFixed(2) : 0;
          } else {
            app.uniqueViews = 0;
            app.leads = 0;
            app.conversionRate = 0;
            app.Cta_Engagement_Count = 0;
          }
        },
        (error: any) => {
        }
      );
  }

