  moveFileToFolder(data, options) {
    options['app'] = options.content_piece['_id'];
    let folder = this.foldersRef.find(f => f['name'] === data.folderName);
    folder || (folder = { company: data.company });
    options.moveFrom && (options['old_name'] = options.moveFrom);
    options.moveFrom || (options['old_name'] = folder['name']);
    console.log(folder, options);
    this._dashboardService.moveFile(Object.assign({}, folder, options))
      .subscribe((f) => {
        if (!folder['name']) {
          const fIndex = this.foldersRef.findIndex(f => f['name'] === options.moveFrom);
          if (fIndex != -1) {
            this.moveFile(options.app, this.foldersRef[fIndex]['apps'], this.foldersContent['leaves']);
            const pinnedAppIndex = this.foldersRef[fIndex]['pinned_apps'].findIndex((app) => app._id === options.app);
            pinnedAppIndex !== -1 && this.foldersRef[fIndex]['pinned_apps'].splice(pinnedAppIndex, 1);
          }
          this.adjustUIForFolder(this.foldersRef[fIndex]['_id'], '');
        } else if (!options.moveFrom) {
          this.moveFile(options.app, this.foldersContent['leaves'], folder['apps']);
          this.adjustUIForFolder('', folder['_id']);
        } else {
          let fIndex = this.foldersRef.findIndex(f => f['name'] === options.moveFrom);
          if (fIndex !== -1) {
            this.moveFile(options.app, this.foldersRef[fIndex]['apps'], folder['apps']);
            const pinnedAppIndex = this.foldersRef[fIndex]['pinned_apps'].findIndex((app) => app._id === options.app);
            pinnedAppIndex != -1 && this.foldersRef[fIndex]['pinned_apps'].splice(pinnedAppIndex, 1);
          }
          this.adjustUIForFolder(this.foldersRef[fIndex]['_id'], folder['_id']);

        }
        if (this.collapseAllRef)
          this.collapseAllRef.nativeElement.checked = false;
        if (this.showSearchView) {
          options.content_piece.folder = Object.assign({}, folder) || null;
        }
        if (this.gotoView) {
          this.gotoItems = this.gotoItems.filter(p => p._id !== options.app);
        }
        const app = options.content_piece;
        app['pinned'] = false;
        setTimeout(() => {
          this.lazyLoadImages([{
            link: ((app.pages[0].visible && app.pages[0].bgImageVisible) || (app.pages[0].bgImageVisible)) ?
              app.pages[0].bgImage :
              this.templatesImages[app.template],
            id: `${app._id}_image`, loaderId: `${app._id}_loader`
          }]);
        }, 100);
        window.toastNotification("Moved Successfully..");
      }, error => {
        window.toastNotification("Error ....");
      });
  }
