  adjustUIForFolder(source, destination) {
    setTimeout(() => {
      destination && jQuery(`#${destination}`).addClass('in');
      jQuery(`#${destination}_caret`).addClass('rotate-icon');
      jQuery(`#${destination}_link`).removeClass('collapsed');

      source && jQuery(`#${source}`).removeClass('in');
      jQuery(`#${source}_caret`).removeClass('rotate-icon');
      jQuery(`#${source}_link`).addClass('collapsed');

    }, 100);
  }
  initalizeFolderSettings(folders = this.foldersContent['folders']) {
    setTimeout(() => {
      folders.length && folders[0]['apps'].length && this.adjustUIForFolder('', folders[0]['_id']);
      let slicedOne = folders.slice(1, folders.length);
      slicedOne.forEach(element => {
        this.adjustUIForFolder(element['_id'], '');
      });
    }, 100);
    return '';
  }

  openIntercom(message = '') {
    window.Intercom('showNewMessage', message);
  }
  handleSearchView(event) {

    switch (event.operation) {
      case 'show-dashboard-view': this.showDashboardSearch(event.items); break;
      case 'back-to-default-view': this.backToDefaultView(); break;

    }
  }
  backToDefaultView() {
    this.showSearchView = false;
    this.gotoItems.forEach(app => (delete app.folder));
    this.gotoItems = [];
    this.gotoView = false;
    this.searched_items.forEach(app => (delete app.folder));
    this.searched_items = [];
    this.loadAllImages();
  }
