  loadFolderImages(folders) {
    folders.forEach((folder) => {
      let images = [];
      if (folder['apps'].length) {
        images = [...images, ...folder['apps'].map((app) => {
          return ({
            link: ((app.pages[0].visible && app.pages[0].bgImageVisible) || (app.pages[0].bgImageVisible)) ?
              app.pages[0].bgImage :
              this.templatesImages[app.template],
            id: `${app._id}_image`, loaderId: `${app._id}_loader`
          });
        })];
      }
      if (folder['pinned_apps'].length) {
        images = [...images, ...folder['pinned_apps'].map((app) => {
          return ({
            link: ((app.pages[0].visible && app.pages[0].bgImageVisible) || (app.pages[0].bgImageVisible)) ?
              app.pages[0].bgImage :
              this.templatesImages[app.template],
            id: `${app._id}_image_pin`, loaderId: `${app._id}_loader_pin`
          });
        })];
      }
      images.length && this.lazyLoadImages(images);
    });
  }
  collapseAll(value) {
    if (value) {
      this.foldersRef.forEach((f) => {
        this.adjustUIForFolder(f['_id'], '');
      })
    } else {
      this.initalizeFolderSettings(this.foldersRef);
    }
  }
