import { OnInit, AfterViewInit, ViewEncapsulation, Component, Output, EventEmitter, Input, ViewChildren } from '@angular/core';
import { Script, SubDomainService, FeatureAuthService, CompanyService } from 'app/shared/services';

declare var Swiper: any;
declare var window: any;
declare var jQuery: any;

@Component({
    selector: 'og-post-login-mobile',
    templateUrl: './post-signup-mobile.template.html',
    styleUrls: ['./../post-signup.component.css',
        '../../../../../../assets/template/css/selectize.default.css',
        '../../../../../../assets/css/swiper.min.css'],
    encapsulation: ViewEncapsulation.None
})
export class PostSignUpMobileComponent implements OnInit, AfterViewInit {
    @ViewChildren('inviteEmail') inviteEmails;
    canInviteUsers: boolean;
    swiper: any;
    inviteUsers: Array<{ email: string, role: string, hide: boolean, error?: string, touched: boolean }> = [];
    premadeIndustries: Array<{ industry: string, selected?: boolean }> = [];
    premadeCalcs: Set<any> = new Set();
    premadeCalcsToDisplay: Array<any> = [];
    arrayFrom = Array.from;
    userInviteFormInvalid: boolean;
    specificIdea = '';
    @Output()
    closePostLogin: EventEmitter<any> = new EventEmitter();
    @Input() showInviteUsersPopup;

    constructor(private scriptService: Script,
        private subDomainService: SubDomainService,
        public featureAuthService: FeatureAuthService,
        private companyService: CompanyService) {
    }

    ngOnInit() {
        this.featureAuthService.getPremadeIndustries().subscribe((industries: any) => {
            this.premadeIndustries.push(industries.industries);
            const i = industries.industries.findIndex((ind) => ind.toLowerCase() === 'funnels');
            i !== -1 && industries.industries.splice(i, 1);
            this.premadeIndustries = industries.industries
                .map(industry => ({ industry, selected: false }));
        });

        const currentCompany = this.subDomainService.currentCompany;
        if (currentCompany.agency || currentCompany.is_appsumo_created
            || currentCompany.deal_refered != null) {
            this.canInviteUsers = false;
        } else {
            this.canInviteUsers = true;
        }

        this.inviteUsers.push({ email: '', role: '', hide: false, touched: false }, { email: '', role: '', hide: false, touched: false });
    }

    ngAfterViewInit() {
        jQuery('#dashboard').addClass('hide');
        const self = this;
        this.scriptService.load('swiper', 'selectize').then((data) => {
            // Initialize Swiper
            this.swiper = new Swiper('.swiper-container-post-signup', {
                spaceBetween: 30,
                observer: true,
                pagination: '.swiper-pagination',
                paginationClickable: true,
                paginationBulletRender: function (className, index) {
                    return '<span class="swiper-pagination-bullet"></span>';
                },
                onSlideChangeStart: function (swiper) {
                    const slideSelector = `.abc${swiper.activeIndex - 1}`;
                    jQuery('.dummy').removeClass('double-post-signup');
                    jQuery(slideSelector).addClass('double-post-signup');
                    // if (swiper.activeIndex === 1 && self.premadeCalcs.size === 0) {
                    // }
                },

            });

            if (this.canInviteUsers && this.showInviteUsersPopup) {
                this.swiper.removeSlide([0, 2, 3, 4, 5]);
            } else if (this.showInviteUsersPopup && !this.canInviteUsers) {
                this.closePostLogin.emit({});
                jQuery('#dashboard').removeClass('hide');
            } else {
                // only fetch premade if it s post signup
                this.getPremadeCalcs({ industry: 'Trending', selected: true }, { checked: true });
                this.getPremadeCalcs({ industry: 'Funnels', selected: true }, { checked: true });
            }

            this.inviteUsers.forEach((user, index) => {
                jQuery(`#role${index}`).selectize({});

                // jQuery('*[data-value="select"]').addClass('select-roll-color');
                jQuery(`#role${index}`)[0].selectize.on('change', () => {
                    const role = jQuery(`#role${index}`)[0].selectize.getValue();
                    if (role === 'select') {
                        self.inviteUsers[index].touched = false;
                    } else {
                        self.inviteUsers[index].touched = true;
                    }
                    self.inviteUsers[index].role = role;
                });
                jQuery('.selectize-wrapper .selectize-input input').prop('disabled', true);

            });
        });
    }

    validateUsers() {
        this.inviteUsers.forEach((user) => {
            user.error = '';
            // when role is selected but no email
            if (user.role && user.role !== 'select' && !user.email) {
                user.error = 'Please enter your email';
            }

            // when email is entered but no role
            if ((!user.role || user.role === 'select') && user.email) {
                user.error = 'Please choose a role';
            }

            // validate email
            if (user.email && !this.validateEmail(user.email)) {
                user.error = 'Please enter a valid email';
            }
        });

        this.userInviteFormInvalid = false;
        this.inviteUsers.forEach(user => {
            if (user.error) { this.userInviteFormInvalid = true; }
        });
        if (!this.userInviteFormInvalid) {
            const users = this.inviteUsers.filter(user => {
                if (user.hide === false) {
                    return user.email && user.role;
                }
            }).map(user => {
                if (user.email && user.role) {
                    return {
                        email: user.email,
                        role: user.role
                    };
                }
            });
            this.companyService.addMultipleUser(users, this.subDomainService.subDomain.company_id)
                .subscribe((success: any) => {
                });
            if (this.showInviteUsersPopup) {
                this.closePostLogin.emit({});
                jQuery('#dashboard').removeClass('hide');
            } else {
                this.swiper.slideNext();
            }
        }
    }

    getPremadeCalcs(premadeIndustry: { industry: string, selected: boolean }, value) {
        premadeIndustry.selected = value.checked;
        if (premadeIndustry.selected) {
            this.featureAuthService.getPremadeCalculators(premadeIndustry.industry)
                .subscribe(calcs => {
                    calcs.forEach(calc => this.premadeCalcs.add(calc));
                    this.premadeCalcsToDisplay = this.shuffleArray(Array.from(this.premadeCalcs).slice(0, 3));
                });
        } else {
            this.premadeCalcs.forEach(calc => {
                if (calc.industry === premadeIndustry.industry) {
                    this.premadeCalcs.delete(calc);
                }
            });
            this.premadeCalcsToDisplay = this.shuffleArray(Array.from(this.premadeCalcs).slice(0, 3));
        }
    }

    shuffleArray(array) {
        let currentIndex = array.length, temporaryValue, randomIndex;
        // While there remain elements to shuffle...
        while (0 !== currentIndex) {
            // Pick a remaining element...
            randomIndex = Math.floor(Math.random() * currentIndex);
            currentIndex -= 1;

            // And swap it with the current element.
            temporaryValue = array[currentIndex];
            array[currentIndex] = array[randomIndex];
            array[randomIndex] = temporaryValue;
        }

        return array;
    }

    changeRole(role: string, index: number) {
        this.inviteUsers[index].role = role;
    }

    removeTextBox(index: number) {
        const $select = jQuery(`#role${index}`).selectize();
        const control = $select[0].selectize;
        control.clear();

        this.inviteUsers[index].email = '';
        this.inviteUsers[index].touched = false;
        //   this.inviteUsers[index].hide = true;
    }

    addTextbox() {
        const self = this;
        this.inviteUsers.push({ email: '', role: '', hide: false, touched: false });
        setTimeout(() => {
            const index = this.inviteUsers.length - 1;
            jQuery(`#role${index}`).selectize({});
            jQuery('.selectize-wrapper .selectize-input input').prop('disabled', true);
            jQuery(`#role${index}`)[0].selectize.on('change', () => {
                // this.inviteUsers[index].role = jQuery(`#role${index}`)[0].selectize.getValue();

                const role = jQuery(`#role${index}`)[0].selectize.getValue();
                if (role === 'select') {
                    self.inviteUsers[index].touched = false;
                } else {
                    self.inviteUsers[index].touched = true;
                }
                self.inviteUsers[index].role = role;
                jQuery('*[data-value="select"]').addClass('select-roll-color');
            });
        }, 60);
    }

    gotoDashboard() {
        const selectedIndustries = this.premadeIndustries
            .map((industry: { industry: string, selected: boolean }) => {
                if (industry.selected) { return industry.industry; }
            }).filter(Boolean);
        console.log(selectedIndustries);
        this.closePostLogin.emit({ industries: selectedIndustries });
        jQuery('#dashboard').removeClass('hide');
        jQuery('.new-dashboard.dashboard-outer').removeClass('noScroll');
    }

    skipCard(index?: number) {
        this.swiper.slideNext();
    }

    shareSpecificIdea(specificIdea: string) {
        console.log(specificIdea);
        window.Intercom('update', { mobile_onboarding_specific_idea: specificIdea });
        this.gotoDashboard();
    }

    validateEmail(email: string) {
        const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(email.toLowerCase());
    }

    openLink(url) {
        window.open(url, '_blank');
    }

    close() {
        this.closePostLogin.emit({});
        jQuery('#dashboard').removeClass('hide');
    }

    focusout() {
        // console.log("dhjdj");
        this.inviteEmails.forEach((tag) => {
            console.log(tag);
            tag.nativeElement.blur();
        });
    }
}
