import { SubDomainService } from 'app/shared/services';
import { BuilderConditions } from './../../../../+builder/services/builderConditions.service';
import { Component, OnInit, Input, Output, EventEmitter, ElementRef, ViewChild, SimpleChanges, OnChanges } from '@angular/core';

@Component({
  selector: 'search-view',
  templateUrl: './search-view.component.html',
  styleUrls: ['./search-view.component.css']
})
export class SearchViewComponent implements OnInit, OnChanges {
  searched_items = [];
  @Output() loadImages: EventEmitter<any> = new EventEmitter<any>();
  @Output() onchanges: EventEmitter<any> = new EventEmitter<any>();
  templatesImages: any = {};
  slicedApps = [];
  searchText = '';
  show_all = false;
  focusout = true;
  @Input() hideView = false;
  @Input() isMobileDevice;

  @Input() folders = [];
  @Input() apps = [];
  @ViewChild('searchContainer') searchContainer: ElementRef;

  constructor(private _builderConditions: BuilderConditions,
    public subDomainService: SubDomainService) {
    this.templatesImages = _builderConditions.templatesImages;

  }

  ngOnInit() {
  }
  ngOnChanges(changes: SimpleChanges): void {
    // this.slicedApps=this.apps
    //Called before any other lifecycle hook. Use it to inject dependencies, but avoid any serious work here.
    //Add '${implements OnChanges}' to the class.
    // if (this.searched_items.length) {
    //   this.searchContainer.nativeElement.classList.add('zoomIn');
    //   this.searchContainer.nativeElement.classList.remove('hide');
    //   this.slicedApps = this.searched_items.slice(0, 4);
    //   this.show_all ? this.sendImageLinks(this.searched_items) : this.sendImageLinks(this.slicedApps);

    // } else {

    //   this.searchContainer.nativeElement.classList.remove('zoomIn');
    //   this.searchContainer.nativeElement.classList.add('hide');
    // }
    if(this.folders.length){
      console.log(this.folders);
    }

  }
  showMore() {
    // this.show_all = !this.show_all;
    //this.show_all ? this.sendImageLinks(this.searched_items) : this.sendImageLinks(this.slicedApps);
  }
  sendImageLinks(apps) {
    this.loadImages.emit(apps.map((app) => {
      return ({
        link: ((app.pages[0].visible && app.pages[0].bgImageVisible) || (app.pages[0].bgImageVisible)) ?
          app.pages[0].bgImage :
          this.templatesImages[app.template],
        id: `${app._id}_image_search`, loaderId: `${app._id}_loader_search`
      });
    }));
  }
  searchFolders(value) {
    if (value) {
      value = value.toLowerCase();
      this.searched_items = [].reduce.call(this.folders, function (acc, folder) {
        acc.push(...folder.apps.reduce((arr, app) => {
          if (app['name'].toLowerCase().includes(value)) {
            // const obj =
            //   (({ mode, _id, url, name, templateType, pages }) =>
            //     ({ mode, _id, url, name, templateType, pages, folder: { name: folder['name'], _id: folder['_id'] } }))(app);
            let obj = Object.assign({},app,{folder});
            // console.log("constructed app ",app);
            arr.push(obj);
          }
          return arr;
        }, []));
        return acc;
      }, [], value = value);
      this.searched_items.push(...this.apps.filter(app =>{
        if (app['name'].toLowerCase().includes(value)) {
          app['folder'] = JSON.parse(JSON.stringify({ company: this.subDomainService.currentCompany.id }));
          return true;
        }
        return false;
      }));
      this.searchContainer.nativeElement.classList.add('zoomIn');
      this.searchContainer.nativeElement.classList.remove('hide');
      this.slicedApps = this.searched_items.slice(0, 4);
      this.show_all ? this.sendImageLinks(this.searched_items) : this.sendImageLinks(this.slicedApps);


    } else {
      this.searched_items = [];
      this.searchContainer.nativeElement.classList.remove('zoomIn');
      this.searchContainer.nativeElement.classList.add('hide');
    }

  }
  sendItems(event) {
    this.focusout = true;
  }
  focusIn(value) {
    // if (!this.hideView) {
    // this.hideView = false;
    this.focusout && (this.focusout = false, setTimeout(this.searchFolders.bind(this, value), 0));
    // }
  }
  passSearchItems(value, items) {
    if (value) {
      this.onchanges.emit({ operation: 'show-dashboard-view', items });

    }
  }
  back() {
    this.searchText = '';
    this.onchanges.emit({ operation: 'back-to-default-view' });

  }
}
