import {
    Component,
    EventEmitter,
    OnInit,
    Output,
    ViewEncapsulation,
    Input,
} from '@angular/core';

declare var ga: any;

@Component({
    selector: 'og-post-login',
    templateUrl: './post-signup.component.html',
    styleUrls: ['./post-signup.component.css',
        '../../../../../assets/template/css/selectize.default.css',
        '../../../../../assets/css/swiper.min.css'],
    encapsulation: ViewEncapsulation.None
})
export class PostSignUpComponent implements OnInit {
    inviteUsers: Array<{ email: string, role: string, placeholder: string, hide: boolean, showError: boolean }> = [];
    @Input() showInviteUsersPopup: any;
    @Input() showPhoneNumberPopup: any;
    @Output()
    closePostLogin: EventEmitter<any> = new EventEmitter();
    isMobileDevice: boolean;

    constructor() {
    }

    ngOnInit() {
        console.log(navigator.userAgent);
        // check Mobile and desktop view
        if (/Android|webOS|iPhone|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
            this.isMobileDevice = true;
        } else {
            this.isMobileDevice = false;
        }
    }

    finishPostLogin(event: any) {
        this.closePostLogin.emit(event);
        ga('markettingteam.send', 'event', 'OnboardFlowComplete', 'Submit', 'Onboard Flow Complete');
    }
}
