import { CompanyService } from "./../../../../../shared/services/company.service";
import { SubDomainService } from "./../../../../../shared/services/subdomain.service";
import { Component, OnInit, Input, ViewEncapsulation } from "@angular/core";
import { IntercomService } from "app/shared/services/intercom.service";
import { FeatureAuthService } from "app/shared/services/feature-access.service";
import { CookieService } from "app/shared/services/cookie.service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { EmailValidator } from "./../../../../../shared/validators/email.validator";

declare var jQuery: any;
declare var window: any;
declare var ga: any;

@Component({
  selector: "left-sidebar",
  templateUrl: "./left-sidebar.component.html",
  styleUrls: [
    "./left-sidebar.component.css",
    "./../../../../../../assets/template/css/selectize.default.css",
    "./../../../../../../assets/css/custom-material.css"
  ],
  encapsulation: ViewEncapsulation.None
})
export class LeftSidebarComponent implements OnInit {
  @Input() companyData: any;
  isAdmin = "user";
  currentCompanyUsers: any;
  IncompanyUsersCount: any;
  edit = false;
  inviteUserForm: FormGroup;
  fListVisible;
  message;
  currentUser: any = {};
  error: any;
  adminCount: number;

  @Input() folders: any = [];
  constructor(
    public _intercomService: IntercomService,
    public _featureAuthService: FeatureAuthService,
    public _cookieService: CookieService,
    private fb: FormBuilder,
    private _subdomainService: SubDomainService,
    private _companyService: CompanyService
  ) {
    this.error = {
      status: false,
      message: ""
    };
    this.adminCount = 0;
  }

  ngOnInit() {
    this.inviteUserForm = this.fb.group({
      userName: [
        "",
        Validators.compose([
          Validators.required,
          ,
          Validators.minLength(3),
          Validators.pattern("^[a-zA-Z ]*$")
        ])
      ],
      userEmail: [
        "",
        Validators.compose([Validators.required, EmailValidator.format])
      ],
      userRole: ["MANAGER", Validators.compose([Validators.required])],
      permissions: this.fb.group({
        folder: [],
        all: true
      }),
      userId: [""]
    });
    this.IncompanyUsersCount = this.companyData["companyUsersCount"];
    this.currentCompanyUsers = this.companyData["currentCompanyUsers"];
    this.isAdmin = this._cookieService.readCookie("role");
    this.currentUser = JSON.parse(this._cookieService.readCookie("storage"))[
      "user"
    ];
    this.countAdmin();
  }

  countAdmin() {
    this.adminCount = 0;
    for (let uc of this.currentCompanyUsers) {
      if (uc.user_company.role === "ADMIN") {
        this.adminCount++;
      }
    }
  }

  sendMessageToIntercom(message = "") {
    this.openIntercom(message);
    this._intercomService.triggerClickToIntercom();
  }

  openIntercom(message = "") {
    window.Intercom("showNewMessage", message);
  }
  resetForm(formValue: any = {}) {
    this.inviteUserForm.reset();
    this.inviteUserForm
      .get("permissions")
      .get("folder")
      .setValue([]);
    this.inviteUserForm
      .get("permissions")
      .get("all")
      .setValue(formValue.all === undefined ? true : formValue.all);
    this.inviteUserForm
      .get("userRole")
      .setValue(
        formValue.userRole === undefined ? "MANAGER" : formValue.userRole
      );
  }
  userCheckLimit() {
    this.edit = false;

    this._featureAuthService.setSelectedFeature("Team Getting Bigger?");
    if (
      this._featureAuthService.features.users === -1 ||
      this.IncompanyUsersCount < this._featureAuthService.features.users
    ) {
      // jQuery('.email').addClass('is-empty');
      jQuery("#add-new-user").modal("show");
      // jQuery('#add-teammates').modal('show');
      jQuery("#premiumModal").attr("active", false);
      ga(
        "markettingteam.send",
        "event",
        "Dashboard",
        "Click",
        "Dashboard Add User"
      );
      this.resetForm();
    } else {
      // jQuery('.users').addClass('activegreen limited-label');
      jQuery("#premiumModal").modal("show");
      jQuery(".modal-backdrop").insertAfter("#premiumModal");
      jQuery("#add-new-user").attr("active", false);
    }
  }
  inviteUser() {
    this.message = "";
    jQuery("#btnInvite")
      .text("Please Wait...")
      .attr("disabled", true);
    jQuery("#success-addUser").addClass("hide");

    let formValue = this.inviteUserForm.value;
    if (formValue.permissions.all) {
      formValue.permissions.folder = this.folders.map(f => f._id);
    }

    const addUser = this._companyService
      .addUser(formValue, this._subdomainService.currentCompany.id)
      .subscribe(
        (success: any) => {
          this.resetForm();
          jQuery("#btnInvite")
            .text("Add New User")
            .attr("disabled", false);

          ga(
            "markettingteam.send",
            "event",
            "Dashboard",
            "Submit",
            "Dashboard User Added"
          );

          this.message = "User Invited Successfully!";
          jQuery("#add-new-user").modal("hide");
          window.toastNotification(this.message);
          this.closeLayover();
        },
        (error: any) => {
          let error_code = error.error.code;
          if (
            error_code === "E_UNIQUE_USERNAME_VALIDATION" ||
            error_code === "E_UNIQUE_EMAIL_VALIDATION" ||
            error_code === "E_UNIQUE_UNIDENTIFIED_VALIDATION" ||
            error_code === "E_USER_COMPANY_ALREADY_EXISTS"
          ) {
            this.message =
              " This user is already a part of this company on Outgrow";
          } else if (error_code === "E_USER_LIMIT_EXCEEDED") {
            this.message =
              "You have exceeded the user limit of you plan. Please upgrade to add more users.";
          } else {
            if (
              error.error.err_errors["emails.0.email"].message ===
              "Please use a valid company email address to sign up for Outgrow :)"
            ) {
              this.message =
                "You can only invite users with a valid company email address";
            } else {
              this.message = error.error.err_errors["emails.0.email"]
                ? error.error.err_errors["emails.0.email"].message
                : error.error.err_message;
            }
          }
          jQuery("#success-addUser").removeClass("hide");
          // jQuery('#add-new-user input').val('');
          // jQuery('.name').addClass('is-empty');
          // jQuery('.email').addClass('is-empty');
          // jQuery('#btnInvite').text('Add New User');
          jQuery("#btnInvite").attr("disabled", false);
          jQuery("#dashboardAdduserMessage").html(this.message);
        }
      );
  }
  removeError() {
    jQuery("#success-addUser").addClass("hide");
  }
  closeLayover() {
    setTimeout(function() {
      jQuery(".float-changes-updated")
        .fadeIn()
        .animate({ bottom: -50 }, 800, function() {});
    }, 4000);
  }
  mapForderRefToID(folders) {
    return folders.map(e => e._id) || [];
  }

  callGA() {
    ga(
      "markettingteam.send",
      "event",
      "Dashboard",
      "Click",
      "Dashboard Add Company"
    );
  }
  editUser(user) {
    if (this.adminCount <= 1 && this.currentUser._id === user.id) {
      return;
    }
    this.resetForm();
    setTimeout(() => {
      if (this.isAdmin === "ADMIN") {
        this.edit = true;
        jQuery("#add-new-user").modal("show");
        this.inviteUserForm.get("userId").setValue(user.id);
        this.inviteUserForm.get("userName").setValue(user.name);
        this.inviteUserForm.get("userEmail").setValue(user.emails[0].email);
        this.inviteUserForm.get("userRole").setValue(user.user_company.role);
        this.inviteUserForm
          .get("permissions")
          .get("folder")
          .setValue(user.user_company.permissions.folder || []);
        let permissions = this.inviteUserForm.get("permissions").value;
        permissions["folder"].length === this.folders.length
          ? this.inviteUserForm
              .get("permissions")
              .get("all")
              .setValue(true)
          : this.inviteUserForm
              .get("permissions")
              .get("all")
              .setValue(false);
      }
      console.log(this.inviteUserForm.value);
    }, 0);
  }
  selectedFolders(folders) {
    this.inviteUserForm
      .get("permissions")
      .get("folder")
      .setValue(folders.map(e => e._id));
    console.log(this.inviteUserForm.value);
  }
  async updateUser(formValue) {
    this.error = {
      status: false,
      message: ""
    };
    this.message = "";
    jQuery("#btnInvite")
      .text("Please Wait...")
      .attr("disabled", true);
    if (formValue.permissions.all && !formValue.permissions.folder.length) {
      formValue.permissions.folder = this.folders.map(f => f._id);
    }
    try {
      await this._companyService
        .updateUserCompany(formValue, formValue.userId)
        .toPromise();

      this.message = "User Updated Successfully!";
      let userIndex = this.currentCompanyUsers.findIndex(user => {
        return user.id === formValue.userId;
      });
      if (userIndex !== -1) {
        this.currentCompanyUsers[userIndex].user_company.role =
          formValue.userRole;
        this.currentCompanyUsers[userIndex].user_company.permissions.folder =
          formValue.permissions.folder;
      }
      window.toastNotification(this.message);
      jQuery("#add-new-user").modal("hide");
      this.resetForm(formValue);
      this.closeLayover();
      jQuery("#btnInvite")
        .text("Update USER")
        .attr("disabled", false);
      this.countAdmin();
    } catch (e) {
      jQuery("#btnInvite")
        .text("Update USER")
        .attr("disabled", false);
      this.error = {
        status: true,
        message: e.error.err_message
      };
    }
  }
}
