import { DashboardService } from './../../../../../shared/services/dashboard.service';
import { FeatureAuthService } from './../../../../../shared/services/feature-access.service';
import { SubDomainService } from './../../../../../shared/services/subdomain.service';
import { Component, OnInit, Input, OnChanges, SimpleChanges, ChangeDetectionStrategy, EventEmitter, Output } from '@angular/core';
import { environment } from 'environments/environment';
import { Router } from '@angular/router';
import { CookieService } from 'app/shared/services';
declare var jQuery: any;
declare var window: any;
declare var document: any;
declare var bootbox: any;
declare var ga: any;

@Component({
  selector: 'card',
  templateUrl: './card.component.html',
  styleUrls: ['./card.component.css'],
  // changeDetection: ChangeDetectionStrategy.OnPush
})
export class CardComponent implements OnInit, OnChanges {
  @Input() data: any = {};
  @Input() isMobileDevice?;
  @Output() onChanges: EventEmitter<any> = new EventEmitter<any>();
  @Input() folders = [];
  app: any = {};
  noOfApps = 0;
  userRole: string;
  constructor(public subDomainService: SubDomainService,
    private _featureAuthService: FeatureAuthService,
    private _dashboardService: DashboardService,public _cookieService: CookieService,
    private _router: Router) { }

  ngOnInit() {
    this.userRole = this._cookieService.readCookie('role');
  }
  ngOnChanges(changes: SimpleChanges): void {
    if (this.data && this.data.app) {
      this.app = this.data.app;
      this.noOfApps = this.data.noOfApps;
    }
  }
  getUrl(url) {
    if (!/(http|https)/.test(url)) {
      return environment.LIVE_PROTOCOL + url;
    }
    return url;
  }
  openLive(url: string) {
    if (environment.STATIC_DOMAIN) {
      return environment.LIVE_PROTOCOL + 'livec.' + environment.LIVE_EXTENSION + '/' + url;
    } else {
      return environment.LIVE_PROTOCOL + this.subDomainService.subDomain.sub_domain + '.' + environment.LIVE_EXTENSION + '/' + url;
    }
  }
  mouseEnter($event: any) {
    jQuery($event.target).find('.total-count').css({ 'width': '60px', 'opacity': '1', 'margin-right': '3px' });
  }

  mouseLeave($event: any) {
    jQuery($event.target).find('.total-count').css({ 'width': '0px', 'opacity': '0', 'margin-right': '0px' });
  }
  openOldCalc(app: any, tabName: string) {
    if (this.isMobileDevice) {
      return;
    }
    localStorage.setItem('project', app._id);
    localStorage.setItem('tab-selected', (app.templateType == 'Ecom') ? 'product_list' : tabName);
    window.location.href = environment.PROTOCOL + this.subDomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + (app.url == 'builder-demo' ? '/' : '/builder/') + app.url;
  }
  callGA(opt: string, data?) {
    this.onChanges.emit({ operation: 'google-analytics', opt, data });
  }
  copyLiveURL(url: string) {
    const copiedURL = this.openLive(url);
    const textField = document.createElement('textarea');
    textField.innerText = copiedURL;
    document.body.appendChild(textField);
    textField.select();
    textField.focus();
    document.execCommand('copy');
    textField.remove();
    window.toastNotification('Copied to Clipboard');
  }
  checkAnalytics(app?: any, tabName?: string) {
    const isAnalyticsAvailable = <boolean>this._featureAuthService.features.analytics.active;
    if (isAnalyticsAvailable) {
      this.openOldCalc(app, tabName);
    } else {
      this._featureAuthService.setSelectedFeature('analytics');
      jQuery('.analytics').addClass('activegreen limited-label');
      jQuery('#premiumModal').modal('show');
      jQuery('.modal-backdrop').insertAfter('#premiumModal');
    }
  }
  selectSubTab(tabName: string) {
    localStorage.setItem('selected-sub-tab', tabName);
  }
  async duplicateApp(app: any, folder = '') {
    try {
      const dataId = app._id;
      const calc_type = (app.templateType == 'Numerical') ? 'Calculator' : ((app.templateType == 'Recommendation') ? 'Quiz' : ((app.templateType == 'Poll') ? 'Poll' : ((app.templateType == 'Ecom') ? 'Ecom Quiz' : ((app.templateType == 'Chatbot') ? 'Chatbot' : 'Graded Quiz'))));
      let isLimitCrossed = false;
      const plan_calc_limit = this._featureAuthService.features.calculators;
      if (this.noOfApps >= plan_calc_limit && plan_calc_limit !== -1) {
        isLimitCrossed = true;
      } else {
        isLimitCrossed = false;
      }
      if (isLimitCrossed) {
        this._featureAuthService.setSelectedFeature('Need more calculators?');
        this.onChanges.emit({ operation: 'premium-popup' });
      } else {
        jQuery('.dashboard-toast').fadeIn().animate({ bottom: 60 }, 800, function () { });
        jQuery('.dash-toast-msg').html(`Duplicating ${calc_type}, Please Wait...`);
        const response = await this._dashboardService.duplicateApp({ id: dataId, agency: false, company: 'false', folder }).toPromise();
        if (jQuery.isEmptyObject(response)) {
          this._router.navigate(['/dashboard']);
        } else {
          response['folder'] = folder || null;
          this.onChanges.emit({ operation: 'fetch-apps', app: response, option:'DUPLICATE'});
          jQuery('.dash-toast-msg').html(`${calc_type} duplicated Successfully`);

          setTimeout(() => {
            jQuery('.dashboard-toast').fadeOut().animate({ bottom: -60 }, 800, function () { });
            // this.adjustUIForFolder('', folder['_id']);
            this.onChanges.emit({ operation: 'adjust-ui', source: null, destination: folder['_id'] });

          }, 2000);
        }
      }
    } catch (error) {
      if (error && error.error && error.error.code === 'E_USER_LIMIT_EXCEEDED') {
        this._featureAuthService.setSelectedFeature('Need more calculators?');
        this.onChanges.emit({ operation: 'premium-popup' });
        setTimeout(function () {
          jQuery('.dashboard-toast').fadeOut().animate({ bottom: -60 }, 800, function () { });
        }, 200);
      } else {
      }
    }

  }
  async duplicateAppForAgency(app, folder = '') {
    try {
      if (this.subDomainService.myCompanyList.length === 1) {
        await this.duplicateApp(app, folder);
        this.callGA('DUPLICATE');
      } else {
        app['folder'] = folder || null;
        this.subDomainService.myCompanyList.forEach(company => {
          company['app'] = app;
        });
        this.onChanges.emit({ operation: 'duplicate-app-agency', data: this.subDomainService.myCompanyList, app });

      }

    } catch (err) {
      console.log('Company List error: ', err);
    }
  }

  moveFileToFolder(data, options) {
    this.onChanges.emit({ operation: 'move-app', data, options });
  }

  async deleteApp(app: any, folder = '') {
    let calc_type = (app.templateType == 'Numerical') ? 'Calculator' : ((app.templateType == 'Recommendation') ? 'Quiz' : ((app.templateType == 'Poll') ? 'Poll' : ((app.templateType == 'Ecom') ? 'eCommerce Quiz' : ((app.templateType == 'Chatbot') ? 'Chatbot' : 'Graded Quiz'))));
    let that: any = this;
    let sub_domain = this.subDomainService.subDomain.sub_domain;
    bootbox.dialog({
      size: 'small',
      message: `<div class="bootbox-body-left">
                        <div class="mat-icon">
                            <i class="material-icons">error</i>
                        </div>
                    </div>
                    <div class="bootbox-body-right">
                        <p class="one-line-para">Are you sure you want to delete this ${calc_type}?</p>
                </div>
            `,
      buttons: {
        cancel: {
          label: "Cancel",
          className: "btn-cancel btn-cancel-hover",
          callback: function () {
            ga('markettingteam.send', 'event', 'Dashboard', 'Click', 'DeleteCalc Confirmation pop up Cancel');
            // _kmq.push(['record', 'Dashboard DeleteCalc Confirmation pop up Cancel']);
          }
        },

        success: {
          label: "OK",
          className: "btn btn-ok btn-hover",
          callback: async function () {
            let response: any = await that._dashboardService.deleteApp({ id: app._id, subDomain: sub_domain, folder }).toPromise();
            // .subscribe(
            //   (response: any) => {
            if (jQuery.isEmptyObject(response)) {
              that._router.navigate(['/dashboard']);
            } else {
              app.status = 'DELETED';
              window.toastNotification(calc_type + ' Deleted Successfully.');
              that.onChanges.emit({ operation: 'delete-app', app, folder });
              // if ((prev - that.maxPageCount) && that.pageNo) that.pageNo--;
            }
            ga('markettingteam.send', 'event', 'Dashboard', 'Click', 'DeleteCalc Confirmation pop up Ok');
            // _kmq.push(['record', 'Dashboard DeleteCalc Confirmation pop up Ok']);
          },
          // (error: any) => {
          // }
          // );
          // }
        }
      }
    });
  }

}
