import { FeatureAuthService } from './../../../../../shared/services/feature-access.service';
import { Component, OnInit, Input, OnChanges, SimpleChanges, EventEmitter, Output } from '@angular/core';
import { environment } from 'environments/environment';

@Component({
  selector: 'premade-card',
  templateUrl: './premade-card.component.html',
  styleUrls: ['./premade-card.component.css']
})
export class PremadeCardComponent implements OnInit, OnChanges {
  @Input() isMobileDevice;
  @Input() data: any = {};
  @Input() getTemplateType: Function;
  @Output() selectedCalc: EventEmitter<any> = new EventEmitter<any>();
  app: any = {};
  constructor(public _featureAuthService: FeatureAuthService) { }

  ngOnInit() {
  }
  ngOnChanges(changes: SimpleChanges): void {
    if (this.data && this.data.app) {
      this.app = this.data.app;
    }
  }
  getUrl(url) {
    if (!/(http|https)/.test(url)) {
      return environment.LIVE_PROTOCOL + url;
    }
    return url;
  }
  createCalc(premade) {
    this.selectedCalc.emit(premade);
  }
}
