import { OnInit, ViewEncapsulation, Component, Output, EventEmitter, AfterViewInit, Input, OnChanges } from '@angular/core';
import { FormGroup, FormBuilder, FormArray, Validators } from '@angular/forms';
import {
    CompanyService, MembershipService, CookieService, FeatureAuthService,
    SubDomainService, Script, UserService
} from 'app/shared/services';

declare var window: any;
declare var jQuery: any;
declare var ga: any;
@Component({
    selector: 'og-post-login-web',
    templateUrl: './post-signup-web.template.html',
    styleUrls: ['./../post-signup.component.css',
        '../../../../../../assets/template/css/selectize.default.css',
        '../../../../../../assets/css/swiper.min.css'],
    encapsulation: ViewEncapsulation.None
})
export class PostSignUpWebComponent implements OnInit, AfterViewInit, OnChanges {

    industries = [];
    inviteUsers: Array<{ email: string, role: string, placeholder: string, hide: boolean, showError: boolean }> = [];
    @Input() showInviteUsersPopup = false;
    @Input() showPhoneNumberPopup = false;
    @Output()
    closePostLogin: EventEmitter<any> = new EventEmitter();

    show = true;
    slideNo = 1;
    define_agency = false;
    define_company = false;
    define_leads = false;
    define_testing = false;
    userInviteForm: FormGroup;

    selectedIndustry = [];
    showYoutube = true;
    googleSearch = false;
    sawSomeoneCalculator = false;
    facebookGroup = false;
    others = false;
    events = false;
    ourBlog = false;
    friend = false;
    podcast = false;
    webinar = false;
    currentPlan: String;
    errorIntercomData = false;
    card: any;
    scriptLoaded = false;
    isError = false;
    currentCompanyUsers: any = [];
    canInviteUsers = false;
    whereDidYouHearAboutUs: Array<string> = [];
    companySize: any;
    cookie: any;
    storage: any;
    canInviteMoreUsers = true;
    phoneNo: any;
    phoneNumberError = false;

    constructor(public fb: FormBuilder, public companyService: CompanyService
        , public subDomainService: SubDomainService, public _membershipService: MembershipService
        , public scriptService: Script, public cookieService: CookieService
        , public featureAuthService: FeatureAuthService,
        public _cookieService: CookieService, public userService: UserService) {
    }

    ngOnInit() {
        const slideNo = this.cookieService.readCookie('slideNo');
        this.slideNo = parseInt(slideNo);
        this.currentPlan = this.subDomainService.currentCompany.billing.chargebee_plan_id;
        const currentCompany = this.subDomainService.currentCompany;
        this.cookie = this._cookieService.readCookie('storage');
        this.storage = this.cookie != null ? JSON.parse(this.cookie) : '';
        jQuery('.slide-inviteTeam').addClass('hide');
        // this.slideNo && this.slideNo > 0 && this.slideNo < 4 &&
        if (this.slideNo && this.slideNo > 0 && this.slideNo <= 5) {
            jQuery('*[data-slide="' + this.slideNo + '"]').siblings().addClass('hide');
            jQuery('*[data-slide="' + this.slideNo + '"]').removeClass('hide');
            jQuery('*[data-slide="' + this.slideNo + '"]').addClass('animate-right');
        } else {
            this.slideNo = 1;
        }

        this.subDomainService.company_props.subscribe((data) => {
            if (data) {
                if (data.agency || data.is_appsumo_created
                    || data.deal_refered != null) {
                    this.canInviteUsers = false;
                } else {
                    this.canInviteUsers = true;
                }
            }
        });

        this.userInviteForm = this.fb.group({
            users: this.fb.array([
                this.getUserFields(),
                this.getUserFields(),
                this.getUserFields()
            ])
        });

        this.inviteUsers.push({
            email: '', role: '', placeholder: 'cmo@yourcompany.com',
            hide: false, showError: false
        });
        this.inviteUsers.push({
            email: '', role: '', placeholder: 'vpmarketing@yourcompany.com',
            hide: false, showError: false
        });
        this.inviteUsers.push({
            email: '', role: '', placeholder: 'digital@yourcompany.com',
            hide: false, showError: false
        });

        const self = this;
        jQuery(document).on('click', '.btn-next', function (e) {
            self.showYoutube = false;
            let result = true;

            if (self.slideNo !== 4) {
                self.isError = false;

                self.errorIntercomData = false;
                if (self.slideNo === 2) {

                    (<any>self.userInviteForm.controls['users']).controls.forEach((control, index) => {
                        control.controls['email'].markAsPristine();
                    });

                    result = self.inviteUsers.reduce((acc, user) => {
                        if (user.email) {
                            acc.push(user);
                        }
                        return acc;
                    }, []).every(user => {
                        if (user.role) { return true; }
                    });

                    for (let i = 0; i < self.inviteUsers.length; i++) {
                        const user = self.inviteUsers[i];
                        const control = (<any>self.userInviteForm.controls).users.controls[i].controls;
                        // role is valid but email is empty
                        if (user.role && user.role != 'select' && !user.email) {
                            self.isError = true;
                            control['email'].markAsDirty();
                            control['email'].setErrors({ required: true });
                        }

                        // email is entered(may not be valid) but role in invalid
                        else if (user.email && (!user.role || user.role === 'select')) {
                            control['email'].markAsDirty();
                            self.inviteUsers[i].showError = true;
                            self.isError = true;

                            if (!self.validateEmail(control['email'].value)) {
                                control['email'].setErrors({ emailPattern: true });
                            }
                            if (self.duplicateVerifier(control, i)) {
                                control['email'].setErrors({ duplicate: true });
                            }
                        }
                        // email is entered(may not be valid) and role is valid
                        if (user.email && user.role && user.role !== 'select') {
                            self.inviteUsers[i].showError = false;
                            control['email'].markAsDirty();

                            if (!self.validateEmail(control['email'].value)) {
                                self.isError = true;
                                control['email'].setErrors({ emailPattern: true });
                            }
                            if (self.duplicateVerifier(control, i)) {
                                self.isError = true;
                                control['email'].setErrors({ duplicate: true });
                            }
                        }
                    }
                }
                if (self.slideNo === 3) {
                    self.getData();
                    if (self.whereDidYouHearAboutUs.length === 0) {
                        self.errorIntercomData = true;
                    }
                    if (!self.companySize || self.companySize === 'select') {
                        self.errorIntercomData = true;
                    }
                }
                if (!self.isError && result) {
                    if (self.slideNo === 1 && !self.canInviteUsers) {
                        self.nextSlide();
                        self.nextSlide();
                    } else if (self.slideNo === 2) {
                        if (self.showPhoneNumberPopup && self.showInviteUsersPopup) {
                            self.addTeammates();
                            self.nextSlide();
                        } else if (self.showInviteUsersPopup) {
                            self.addTeammates();
                            self.cancel();
                        } else {
                            self.addTeammates();
                            self.nextSlide();
                        }
                    } else if (self.slideNo === 3 && !self.errorIntercomData) {
                        self.saveDataToIntercom();
                        self.nextSlide();
                    } else if (self.slideNo !== 3) {
                        self.nextSlide();
                    }
                }
            }
        });
    }
    ngOnChanges() {
        if ((this.showInviteUsersPopup)) {
            this.slideNo = 1;
            this.nextSlide();
        }
        // else if (this.showPhoneNumberPopup) {
        //     this.slideNo = 5;
        //     this.cookieService.createCookie('slideNo', this.slideNo + '', 7);

        //     // For Showing the slide of next step
        //     jQuery('*[data-slide="' + this.slideNo + '"]').siblings().addClass('hide');

        //     jQuery('*[data-slide="' + this.slideNo + '"]').removeClass('hide');
        //     jQuery('*[data-slide="' + this.slideNo + '"]').addClass('animate-right');
        // }
    }

    modalClose() {
        // jQuery('#postSignup-mobile').addClass('hide');
    }

    nextSlide() {
        if (this.slideNo === 2 && (this.showInviteUsersPopup)) {
            //     if (this.showInviteUsersPopup && this.showPhoneNumberPopup)
            this.slideNo = 5;
        }
        this.slideNo++;
        this.cookieService.createCookie('slideNo', this.slideNo + '', 7);

        // For Showing the slide of next step
        jQuery('*[data-slide="' + this.slideNo + '"]').siblings().addClass('hide');

        jQuery('*[data-slide="' + this.slideNo + '"]').removeClass('hide');
        jQuery('*[data-slide="' + this.slideNo + '"]').addClass('animate-right');
    }

    ngAfterViewInit(): void {
        const self = this;

        this.scriptService.load('selectize').then(data => {
            this.scriptLoaded = true;

            // jQuery('#selected-industry').selectize({});
            // jQuery('#selected-industry')[0].selectize.on('blur', function () {
            //     self.selectedIndustry = jQuery("#selected-industry")[0].selectize.getValue();
            // });
            if (this.storage.postSignup) {
                this.initializeIndustries();

                jQuery("#company-size").selectize({});
                jQuery("#company-size")[0].selectize.on('change', function () {
                    self.companySize = jQuery("#company-size")[0].selectize.getValue();
                    self.intercomDataSelected();
                });
            }


            for (let i = 0; i < this.inviteUsers.length; i++) {
                jQuery("#role" + i).selectize({});
                jQuery('*[data-value="select"]').addClass('select-roll-color');
                jQuery("#role" + i)[0].selectize.on('change', function () {
                    self.inviteUsers[i].role = jQuery("#role" + i)[0].selectize.getValue();
                    jQuery('*[data-value="select"]').addClass('select-roll-color');
                });
            }
            jQuery('.right-sec .selectize-input input').prop('disabled', true);
            jQuery("#select-role").selectize({});
        });

        /*Question slider indication Functionality */
        // jQuery('.pagination-circle').click(function (e) {
        //     const questionNumber = jQuery(this).data('question');
        //     self.showYoutube = questionNumber === 1 ? true : false;

        //     if (questionNumber < self.slideNo) {
        //         jQuery('*[data-slide="' + questionNumber + '"]').removeClass('animate-right');
        //         jQuery('*[data-slide="' + questionNumber + '"]').addClass('animate-left');
        //     } else {
        //         jQuery('*[data-slide="' + questionNumber + '"]').removeClass('animate-left');
        //         jQuery('*[data-slide="' + questionNumber + '"]').addClass('animate-right');
        //     }
        //     jQuery('*[data-slide="' + questionNumber + '"]')
        //         .removeClass('hide').siblings('div[class*="slide"]').addClass('hide');

        //     self.slideNo = questionNumber;
        //     self.cookieService.createCookie('slideNo', self.slideNo + '', 7);
        // });
    }

    paginateToSlide(slideNo: number) {
        this.showYoutube = slideNo === 1 ? true : false;

        if (slideNo < this.slideNo) {
            jQuery('*[data-slide="' + slideNo + '"]').removeClass('animate-right');
            jQuery('*[data-slide="' + slideNo + '"]').addClass('animate-left');
        } else {
            jQuery('*[data-slide="' + slideNo + '"]').removeClass('animate-left');
            jQuery('*[data-slide="' + slideNo + '"]').addClass('animate-right');
        }
        jQuery('*[data-slide="' + slideNo + '"]')
            .removeClass('hide').siblings('div[class*="slide"]').addClass('hide');

        this.slideNo = slideNo;
        this.cookieService.createCookie('slideNo', this.slideNo + '', 7);
    }

    saveDataToIntercom() {
        window.Intercom('update', {
            'Company Size': this.companySize,
            'signup_source': this.whereDidYouHearAboutUs.toString(),
        });
    }

    getUserFields() {
        return this.fb.group({
            email: ['', Validators.compose([Validators.required,
            Validators.pattern('^(([^<>()\\[\\]\\\\.,;:\\s@"]+(\\.[^<>()\\[\\]\\\\.,;:\\s@"]+)*)|(".+"))@((\\[[0-9]{1,3}\\.[0-9]{1,3}\\.[0-9]{1,3}\\.[0-9]{1,3}\\])|(([a-zA-Z\\-0-9]+\\.)+[a-zA-Z]{2,}))$')])]
        });
    }

    addTextbox() {
        this.currentCompanyUsers = this.inviteUsers.filter(user => !user.hide);

        if (this.featureAuthService.features.users === -1 || this.currentCompanyUsers.length < this.featureAuthService.features.users - 1) {
            this.canInviteMoreUsers = true;
            const control = <FormArray>this.userInviteForm.controls['users'];
            control.push(this.getUserFields());
            this.inviteUsers.push({
                email: '', role: '', placeholder: `Enter teammate's email id`,
                hide: false, showError: false
            });
            const self = this;
            setTimeout(() => {
                const index = this.inviteUsers.length - 1;
                jQuery("#role" + index).selectize({});
                jQuery('*[data-value="select"]').addClass('select-roll-color');
                jQuery('.right-sec .selectize-input input').prop('disabled', true);

                jQuery("#role" + index)[0].selectize.on('change', function () {
                    self.inviteUsers[index].role = jQuery("#role" + index)[0].selectize.getValue();
                    jQuery('*[data-value="select"]').addClass('select-roll-color');
                });
            }, 50);
        } else {
            this.canInviteMoreUsers = false;
        }
    }

    addTeammates() {
        const users = this.inviteUsers.filter(user => {
            if (user.hide === false) {
                return user.email && user.role;
            }
        }).map(user => {
            if (user.email && user.role) {
                return {
                    email: user.email,
                    role: user.role
                };
            }
        });
        this.companyService.addMultipleUser(users, this.subDomainService.subDomain.company_id)
            .subscribe((success: any) => {
            });
    }

    deleteTextbox(index) {
        const control = <FormArray>this.userInviteForm.controls['users'];
        control.controls[index].setErrors(null);
        control.controls[index].reset();
        this.inviteUsers[index].hide = true;
        this.inviteUsers[index].role = '';
        this.inviteUsers[index].email = '';
        this.currentCompanyUsers = this.inviteUsers.filter(user => !user.hide);
    }

    duplicateVerifier(control, index) {
        if (this && this.userInviteForm && control) {
            let value = control;
            if (typeof (control) === 'string') {
                value = control.trim();
            }

            const regex = new RegExp(`("email":"${value}\")`, 'igm');
            const st = JSON.stringify(this.userInviteForm.value);
            const matches = st.match(regex);
            return (matches && matches.length > 1 && this.validateEmail(control))
        }
    }

    validateEmail(email) {
        let re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(email);
    }

    skipSlide() {
        // if (this.slideNo == 2 && (this.showInviteUsersPopup || this.showPhoneNumberPopup)) {
        //     if (this.showInviteUsersPopup && this.showPhoneNumberPopup)
        //         this.slideNo = 5;
        // } else
        this.slideNo++;
        this.cookieService.createCookie('slideNo', this.slideNo + '', 7);
        // For Showing the slide of next step
        jQuery('*[data-slide="' + this.slideNo + '"]').addClass('animate-right');
        jQuery('*[data-slide="' + this.slideNo + '"]').siblings().addClass('hide');
        jQuery('*[data-slide="' + this.slideNo + '"]').removeClass('hide');
    }

    getData() {
        this.whereDidYouHearAboutUs = [];

        if (this.facebookGroup)
            this.whereDidYouHearAboutUs.push('facebook');
        if (this.googleSearch)
            this.whereDidYouHearAboutUs.push('google');
        if (this.podcast)
            this.whereDidYouHearAboutUs.push('Podcast');
        if (this.sawSomeoneCalculator)
            this.whereDidYouHearAboutUs.push(`Saw Someone's Calculator`);
        if (this.others)
            this.whereDidYouHearAboutUs.push('Others');
        if (this.events)
            this.whereDidYouHearAboutUs.push('events');
        if (this.ourBlog)
            this.whereDidYouHearAboutUs.push('Our Blog');
        if (this.friend)
            this.whereDidYouHearAboutUs.push('Friend');
        if (this.webinar)
            this.whereDidYouHearAboutUs.push('Webinar');
    }

    close() {
        const data = [];
        if (this.define_agency) {
            data.push('Agency looking to work with Outgrow for Clients');
        }
        if (this.define_company) {
            data.push('Company looking to use Outgrow internally');
        }
        if (this.define_leads) {
            data.push('Trying interactive content for qualified leads');
        }
        if (this.define_testing) {
            data.push('Just Testing');
        }
        window.Intercom('update', {
            signup_what_defines_you_best: data.toString()
        });
        this.closePostLogin.emit({ industries: this.selectedIndustry });
        this.cookieService.createCookie('slideNo', '', -1);
    }

    intercomDataSelected() {
        setTimeout(() => {
            if ((this.facebookGroup || this.podcast || this.googleSearch || this.webinar || this.events ||
                this.ourBlog || this.friend || this.sawSomeoneCalculator || this.others) &&
                (!this.companySize || !(this.companySize === 'select'))) {
                this.errorIntercomData = false;
            }
        }, 10);
    }

    initializeIndustries() {
        const $sub = this.featureAuthService.getPremadeIndustries().subscribe((response) => {
            console.log(response);
            this.industries = response.industries || [];
            const i = this.industries.findIndex((ind) => ind.toLowerCase() === 'funnels');
            i !== -1 && this.industries.splice(i, 1);
            setTimeout(() => {
                jQuery('#selected-industry').selectize({

                    options: this.industries.map(ind => ({ name: ind })),
                    searchField: ['name'],
                    labelField: 'name',
                    valueField: 'name'
                });
                jQuery('#selected-industry')[0].selectize.on('blur', () => {
                    this.selectedIndustry = jQuery('#selected-industry')[0].selectize.getValue();
                });
            }, 100);
            $sub.unsubscribe();
        }, (err) => {
            $sub.unsubscribe();
        });
    }
    cancel() {
        this.phoneNumberError = false;
        this.closePostLogin.emit({});
        this.cookieService.createCookie('slideNo', '', -1);
    }
    submitInvitedUsers() {
        if (!this.userInviteForm.valid) {
            // this.isError = true;
        } else {
            this.addTeammates();
            this.cancel();
        }
    }
    savePhone() {
        if (this.phoneNo) {
            const PhoneFormat = /^(?:\(?\+?([0-9]{1,3})\)?[-. ]?)?\(?([0-9]{1,3})\)?[-. ]?([0-9]{3})[-. ]?([0-9]{2,6})$/;
            const UsPhoneFormat = /^(\([0-9]{3}\)\s|[0-9]{3}-)[0-9]{3}-[0-9]{4}$/
            if (PhoneFormat.test(this.phoneNo) || UsPhoneFormat.test(this.phoneNo)) {
                window.Intercom('update', { Phone: this.phoneNo });
                let storage: any = this._cookieService.readCookie('storage');
                storage = JSON.parse(storage);
                this.userService.updateUserPhoneNo(this.phoneNo).subscribe(res => {
                    this.cancel();
                }, err => {
                    console.log(err);
                });
            } else {
                // error display
                this.phoneNumberError = true;
            }
        } else {
            this.phoneNumberError = true;
        }
    }
    callGA() {
        ga('markettingteam.send', 'event', 'Signup', 'Submit', 'Completed', 1);
    }
}
