import { Subject, Observable } from 'rxjs/Rx';
import { DashboardService } from './../../../../../shared/services/dashboard.service';
import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';

@Component({
  selector: 'premade-container',
  templateUrl: './premade-container.component.html',
  styleUrls: ['./premade-container.component.css']
})
export class PremadeContainerComponent implements OnInit {
  @Input() premadeFolder: any;
  premades = [];
  @Input() defaultImage = '';
  @Input() isMobileDevice;
  @Output() loadImages: EventEmitter<any> = new EventEmitter<any>();
  @Input() getTemplateType: Function;
  @Output() createPremadeCalc: EventEmitter<any> = new EventEmitter<any>();

  allPremades = [];
  isPremadeAvailable = new Subject();
  constructor(private _dashboardService: DashboardService) { }

  ngOnInit() {
    this._dashboardService.getPremades().subscribe((premades) => {
      premades.length && this.filterPremadesAcc(premades);
    });
  }
  filterPremadesAcc(premades) {
    this.allPremades = premades;
    this.setPremades(premades);
  }
  setPremades(premades) {
    const $premades = Observable.timer(100, 100)
      .takeUntil(this.isPremadeAvailable)
      .subscribe((t) => {
        if (this.premadeFolder && this.premadeFolder['industries']) {
          this.premades = this.getPremadesByIndustries(premades, this.premadeFolder['industries']);
          const array: any = [];
          this.premades.forEach((premade) => {
            array.push({ link: (premade['media'] || this.defaultImage), id: `${premade._id}_image`, loaderId: `${premade._id}_loader` });
          });
          this.loadImages.emit(array);
          this.isPremadeAvailable.next();
          $premades.unsubscribe();
        }
      });
  }
  getPremadesByIndustries(premades, industries) {
    const premadeCalcs = industries.reduce((acc, industry) => {
      const calcs = premades.filter(p => p['industry'] === industry);
      acc.push(...calcs);
      return acc;
    }, []);
    return premadeCalcs;
  }
  createCalc(premade) {
    this.createPremadeCalc.emit(premade);
  }
}
