import { BuilderService } from './../../../+builder/services/builder.service';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { HttpBaseService } from '../../../../shared/services/base.service';
import { HttpClient } from '@angular/common/http';

@Injectable()
export class CalculatorAnalytics extends HttpBaseService {

  constructor(public _http: HttpClient, public builderService: BuilderService) {
    super();
  }

  getTrafficStats(data: any) {
    let URL = this._url + (this.builderService.isDemo ? '/analytic-demo/calculator_stats' : '/analytic/calculator_stats');
    return this._http.post(URL, data, this.post_options())
      .map(this.extractData)
      .catch(this.handleError);
  }
  exportToSheet(data: any) {
    let URL = this._url + '/analytic/export_to_sheet';
    return this._http.post(URL, data, this.post_options())
      .map(this.extractData)
      .catch(this.handleError);
  }
  getTotalLeads(data: any) {
    let URL = this._url + '/analytic/get_total_leads';
    return this._http.post(URL, data, this.post_options())
      .map(this.extractData)
      .catch(this.handleError);
  }
  exportToSheetAsync(data: any) {
    let URL = this._url + '/analytic/export_to_sheet_async';
    return this._http.post(URL, data, this.post_options())
      .map(this.extractData)
      .catch(this.handleError);
  }
  //getleads
  getAvgOfLeads(data: any): Observable<any> {
    return this._http.post(
      this._url + '/analytic/get_leads_avg',
      data,
      this.post_options()
    )
      .map(this.extractData)
      .catch(this.handleError);
  }
  deleteLeads(data: any): Observable<any> {
    return this._http.post(
      this._url + '/analytic/delete_leads',
      data,
      this.post_options()
    )
      .map(this.extractData)
      .catch(this.handleError);
  }
  archiveLead(data: any): Observable<any> {
    return this._http.post(
      this._url + '/analytic/archive_leads',
      data,
      this.post_options()
    )
      .map(this.extractData)
      .catch(this.handleError);
  }
  getStats(key: any, calcId: string): Observable<any> {
    let url = this._url + (this.builderService.isDemo ? '/analytic-demo/get_stats' : '/analytic/get_stats');
    return this._http.post(
      url,
      { key: key, calc: calcId },
      this.post_options()
    )
      .map(this.extractData)
      .catch(this.handleError);
  }

  getLeadsCount(companyId: any): Observable<any> {
    return this._http.get(
      this._url + '/analytic/leads/count/' + companyId,
      this.get_options())
      .map(this.extractData)
      .catch(this.handleError);
  }

  getFunnels(calcId: string): Observable<any> {
    let url = this._url + (this.builderService.isDemo ? '/_f-demo/get_funnels' : '/_f/get_funnels');
    return this._http.post(url,
      { calc: calcId },
      this.post_options()
    )
      .map(this.extractData)
      .catch(this.handleError);
  }

  getQuestionStatsData(funnelId: string, calcId: string, funnelDuration: string, company: string): Observable<any> {
    let url = this._url + (this.builderService.isDemo ? '/_f-demo/_f_q_d/funnelId/' + funnelId + '/calcId/' + calcId + '/funnelDuration/' + funnelDuration + '/_c/' + company : '/_f/_f_q_d/funnelId/' + funnelId + '/calcId/' + calcId + '/funnelDuration/' + funnelDuration + '/_c/' + company);
    return this._http.get(url, this.get_options())
      .map(this.extractData)
      .catch(this.handleError);
  }


  getFunnelDetailsData(funnelId: string, calcId: string, company: string,skip:Number,limit:Number): Observable<any> {
    let url = this._url + (this.builderService.isDemo ? '/_f-demo/_f_det_d/funnelId/' + funnelId + '/calcId/' + calcId + '/_c/' + company +'/skip/'+skip+'/limit/'+limit : '/_f/_f_det_d/funnelId/' + funnelId + '/calcId/' + calcId + '/_c/' + company +'/skip/'+skip+'/limit/'+limit);
    return this._http.get(url, this.get_options())
      .map(this.extractData)
      .catch(this.handleError);
  }

  getUserDetailsLeads(data: any, postUrl: string): Observable<any> {
    let url = this._url + `/analytic/${postUrl}`;
    return this._http.post(url,
      data,
      this.post_options()
    )
      .map(this.extractData)
      .catch(this.handleError);
  }

  getSegments(appId: any, isApp: boolean) {
    return this._http.get(this._url + '/analytic/' + appId + '/app_filter_segment/' + String(isApp), this.get_options())
      .map(this.extractData)
      .catch(this.handleError)
  }

  saveAppSegment(appConfig: any) {
    return this._http.post(this._url + '/analytic/save_filter_segment', { appConfig }, this.post_options())
      .map(this.extractData)
      .catch(this.handleError);
  }

  getCompanyUsage(companyId: any) {
    return this._http.get(this._url + '/analytic/getCurrentCompanyUsage/' + companyId, this.get_options())
      .map(this.extractData)
      .catch(this.handleError)
  }

  savePerformanceGrade(appId:any,performanceGrade: any, total: any) {
    return this._http.post(this._url + '/analytic/save_performance_grade', {appId, performanceGrade, total }, this.post_options())
    .map(this.extractData)
    .catch(this.handleError);
  }

}
