import { BuilderConditions } from './../../../../+builder/services/builderConditions.service';
import { DashboardService } from './../../../../../shared/services/dashboard.service';
import {
  Component, OnInit, Input, OnChanges, SimpleChanges, ViewChild, ElementRef,
  Output, EventEmitter, ChangeDetectionStrategy
} from '@angular/core';
declare var jQuery: any;
declare var bootbox: any;
@Component({
  selector: 'folder-container',
  templateUrl: './folder-container.component.html',
  styleUrls: ['./folder-container.component.css'],
  // changeDetection: ChangeDetectionStrategy.OnPush
})
export class FolderContainerComponent implements OnInit, OnChanges {
  @Input() folders = [];
  @Input() folder = {};

  @Input() data: any = {};
  @Output() loadImages: EventEmitter<any> = new EventEmitter<any>();
  templatesImages: any = {};

  noOfApps = 0;

  @Input() isMobileDevice?;
  public readonly unpublishFolder = 'unpublished content';
  public readonly publishFolder = 'published content';
  // foldersRef = [];
  @ViewChild('collapse') collapseAllRef: ElementRef;
  @Output() onChanges: EventEmitter<any> = new EventEmitter<any>();
  constructor(private _dashboardService: DashboardService,
    public _builderConditions: BuilderConditions) { }

  ngOnInit() {
    this.templatesImages = this._builderConditions.templatesImages;
  }

  ngOnChanges(changes: SimpleChanges): void {
    // if (this.folders.length) {
    // this.foldersRef = this.folders;
    this.noOfApps = this.data.noOfApps;
    console.log("changes", this.folders);
    // }
  }
  addClass(cls, element) {
    jQuery('#' + element).addClass(cls);
  }
  iconClicked(ele, folder, aTagId) {
    const caret = jQuery(`#${ele}`);

    if (caret[0].classList.contains('rotate-icon')) {
      caret[0].classList.remove('rotate-icon');
    } else {
      caret[0].classList.add('rotate-icon');
    }
    setTimeout(() => {
      if (caret[0] && caret[0].classList.contains('rotate-icon')) {
        if (this.data.collapseAllRef.nativeElement.checked && folder && folder['apps'] && folder['apps'].length) {
          const anchor = jQuery(`#${aTagId}`);
          if (anchor && anchor[0] && !anchor[0].classList.contains('collapsed')) {
            this.onChanges.emit({ operation: 'collapse', value: false, folder, caret, aTagId });

          }
        }
      }
    }, 0);
  }
  initializeForm(folder) {
    this.onChanges.emit({ operation: 'folder-update', folder });
  }
  removeFolder(folder, index) {
    this.onChanges.emit({ operation: 'folder-remove', folder, index });

  }
  excludeFolder(folderToExclude, folders) {
    return folders.filter(f => f['name'] !== folderToExclude);
  }
  async pinToTop(folder, app) {
    try {
      app['pinned'] = true;
      folder['pinned_apps'].unshift(app);
      await this._dashboardService.pinTotop({ appId: app['_id'], folder }).toPromise();
      this.loadImages.emit([{
        link: ((app.pages[0].visible && app.pages[0].bgImageVisible) || (app.pages[0].bgImageVisible)) ?
          app.pages[0].bgImage :
          this.templatesImages[app.template],
        id: `${app._id}_image_pin`, loaderId: `${app._id}_loader_pin`
      }]);
    } catch (e) {
      console.log(e);
    }
  }

  async unpinfromoTop(folder, app) {
    try {
      const pinnedApp = folder['pinned_apps'].findIndex((a) => a._id === app._id);
      folder['pinned_apps'].splice(pinnedApp, 1);
      app['pinned'] = false;
      await this._dashboardService.unpinfromtop({ appId: app['_id'], folder }).toPromise();
    } catch (e) {
      console.log(e);
    }
  }
  emitChanges(event) {
    switch (event.operation) {
      case 'pin-app': this.pinToTop(event.folder, event.app); break;
      case 'unpin-app': this.unpinfromoTop(event.folder, event.app); break;
      default: this.onChanges.emit(event);
    }
  }

}
