import {
  Component,
  OnInit,
  Input,
  ElementRef,
  ViewChild,
  Output,
  EventEmitter,
  SimpleChanges
} from "@angular/core";
import { Validators, FormBuilder, FormGroup } from "@angular/forms";
import { SubDomainService, DashboardService } from "app/shared/services";
declare var jQuery: any;
declare var window: any;
@Component({
  selector: "og-folder-model",
  templateUrl: "./folder-model.component.html",
  styleUrls: ["./folder-model.component.css"]
})
export class FolderModelComponent implements OnInit {
  @ViewChild("folderBtn") btnRef: ElementRef;
  @Input() add: boolean;
  @Input() folderOptions: any;
  @Output() createFolderUpdate = new EventEmitter();
  @Output() updateFolderUpdate = new EventEmitter();
  folderForm: FormGroup;
  Message;
  constructor(
    public fb: FormBuilder,
    public subDomainService: SubDomainService,
    public _dashboardService: DashboardService
  ) {this.folderForm = this.fb.group({
    _id: [""],
    name: [
      "",
      Validators.compose([Validators.required, Validators.minLength(3)])
    ],
    old_name: [""],
    company: [""]
  });
    }

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges): void {

    if (!this.add) {
      this.setFolderForm(this.folderOptions);

    }
    else  {
      this.addButton("",true);
      this.folderForm.reset();
      this.add=true;

    }

  }

  folderOps(data, isNew, btnRef) {


    isNew && this.createFolder(data, btnRef);
    isNew || this.updateCompanyFolder(data, btnRef);
  }

  createFolder(data, btnRef) {
    // jQuery('#btnAddFolder').text('Please Wait...').attr('disabled', true);
    this.disableButton(btnRef);
    data["name"] = data["name"].toLowerCase().trim();
    data["company_id"] = this.subDomainService.currentCompany.id;
    this._dashboardService.createCompanyFolder(data).subscribe(
      data => {
        this.createFolderUpdate.emit(data);

        // this.foldersContent['folders'] = [data,...this.foldersContent['folders']];

        // this.foldersRef = this.foldersContent['folders'];

        // this.initalizeFolderSettings();

        this.addButton(btnRef, false);
        jQuery("#success-addFolder").addClass("hide");
        jQuery("#add-collection").modal("hide");
        window.toastNotification(`Folder Created Successfully..`);
      },
      error => {
        // jQuery('#btnAddFolder').text('Add New Folder');
        // jQuery('#btnAddFolder').attr('disabled', false);
        this.addButton(btnRef, false);
        jQuery("#dashboardAddFolderMessage").text(error.error.err_message);
        jQuery("#success-addFolder").removeClass("hide");
      }
    );
  }

  updateCompanyFolder(data, btnRef) {
    // jQuery('#btnAddFolder').text('Please Wait...').attr('disabled', true);
    this.disableButton(btnRef);
    data["name"] = data["name"].toLowerCase().trim();

    this._dashboardService.updateCompanyFolder(data).subscribe(
      response => {
        this.updateFolderUpdate.emit(response);
        // let index = this.foldersContent['folders'].findIndex(folder => folder._id == response._id);
        // this.foldersContent['folders'][index].name = response.name;

        this.editButton(btnRef);
        jQuery("#success-addFolder").addClass("hide");
        jQuery("#add-collection").modal("hide");
        window.toastNotification(`Folder Updated Successfully..`);
      },
      error => {
        this.editButton(btnRef);
        jQuery("#dashboardAddFolderMessage").text(error.error.err_message);
        jQuery("#success-addFolder").removeClass("hide");
      }
    );
  }

  setFolderForm(folder) {
    console.log(">>>>>>>>>>>>>>>>>>>>>>>>>>>>>fo", folder);
    this.folderForm.get("name").setValue(folder["name"]);
    this.folderForm.get("old_name").setValue(this.folderForm.get("name").value);
    this.folderForm.get("company").setValue(folder["company"]);
    this.editButton("");
  }

  disableButton(btn) {
    this.btnRef.nativeElement.textContent = "Please Wait...";
    this.btnRef.nativeElement.disabled = true;
  }

  addButton(btn, disabled) {
    this.btnRef.nativeElement.textContent = "Add Folder";
    this.btnRef.nativeElement.disabled = disabled;
  }

  editButton(btn) {
    this.btnRef.nativeElement.textContent = "Update Folder Name";
    this.btnRef.nativeElement.disabled = false;
  }
}
